@import url(https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;400;500&display=swap);
:root {
  --bg: #1a1a1c;
  --bg2: #4e4e50;
  --red1: #c3083f;
  --red2: #940741;
  --red3: #6f2232;
  --orange: #ef7f00;
}

html,
body,
#root {
  margin: 0;
  height: 100%;
  background-image: linear-gradient(
    to bottom right,
    #c3083f 40%,
    #ef7f00 100%
  );
  background-image: linear-gradient(
    to bottom right,
    var(--red1) 40%,
    var(--orange) 100%
  );
}

.wrapper {
  display: grid;
  margin: 2vh;
  height: 90vh;
  grid-gap: 2vh;
  gap: 2vh;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "timer card";
}

@media (max-width: 768px) {
  .wrapper {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
      "timer "
      "card";
  }
}

.header {
  display: flex;
  font-family: "DM Mono", monospace;
  justify-content: space-between;
  align-items: center;
  height: 6vh;
  background-color: var(--bg);
  box-shadow: 0 0 20px black;
}

.header--name {
  margin-left: 10%;
  font-size: 5vh;
  background-image: linear-gradient(to right, var(--orange), var(--red1));
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  color: white;
}

.header--login {
  margin-right: 10%;
}

@media (max-width: 500px) {
  .header--name {
    margin-left: 4%;
    font-size: 3vh;
  }
  .header--login {
    margin-right: 4%;
  }
}

.btn {
  font-family: "DM Mono", monospace;
  padding: 0 2vh;
  outline: none;

  background-image: linear-gradient(
    to right,
    var(--red1) 0%,
    var(--orange) 51%,
    var(--red1) 100%
  );
  background-size: 200% auto;
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;

  border-style: solid;
  border-image-source: linear-gradient(
    to right,
    var(--red1) 0%,
    var(--orange) 51%,
    var(--red1) 100%
  );
  border-width: 0.25vh;
  border-image: linear-gradient(to right, var(--red1), var(--orange)) 1;

  transition: 0.5s;
}

.btn:hover {
  transform: scale(1.1);
  background-position: right center;
  border-image: linear-gradient(to left, var(--red1), var(--orange)) 1;
}

.btn:active {
  transform: scale(0.5);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg);
  box-shadow: 0 0 13px black;
  border-radius: 30px;
  outline: 0.25vh solid white;
  outline-offset: -3vh;
}


